<template>
  <div class="menu">

    <div class="skeleton-box" v-if="isLoading">
      <van-skeleton :row="1" :row-width="['100%']" />
      <div class="skeleton-menu">
        <div class="skeleton-menu-left">
          <van-skeleton :row="3" />
          <div class="skeleton-item">
            <van-skeleton :row="3" />
          </div>
          <div class="skeleton-item">
            <van-skeleton :row="3" />
          </div>
          <div class="skeleton-item">
            <van-skeleton :row="3" />
          </div>
        </div>
        <div class="skeleton-menu-right">
          <van-skeleton :row="5" />
          <div class="skeleton-item">
            <van-skeleton :row="5" />
          </div>
          <div class="skeleton-item">
            <van-skeleton :row="5" />
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <div class="menu-search">
        <van-search placeholder="输入商品名称" v-model="msg" shape="round" @search="search(msg)"  @input="isShow(msg)"/>
      </div>

      <div class="menu-box">

        <div class="aside-menu">
          <div class="aside-menu-item" :class="{active: activeIndex === index}" v-for="(item, index) in typeData" :key="index" @click="toggleType(index, item.type)">{{item.typeDesc}}</div>
        </div>

        <div class="menu-content">

          <div class="menu-product" v-show="isOpen == 0">

            <div class="m-item" v-for="(item, index) in productData" :key="index" @click="viewDetail(item.pid)">
              <Product :pro="item">
                  <template #price>
                      
                  </template>
              </Product>
            </div>

          </div>
          <!-- 搜索内容 -->
          <div class="search-content" v-show="isOpen == 1">
              <div class="search-item" v-for="(item, index) in AllSearchData" :key="index" @click="toDetail(item.pid)">
                  <Product :pro="item" ></Product>
              </div>
          </div>
          <!-- 空 -->
          <div class="empty" v-show="isOpen == 2">
              <van-empty description="没有找到相关商品!" />
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Product from '../components/Product.vue'
export default {
  name: 'Menu',
  components: {
    Product
  },

  data() {
    return {

      //加载中, 显示骨架屏
      isLoading: true,

      activeIndex: 0,

      //商品类型数据
      typeData: [],

      //商品数据
      productData: [],
      // 输入值
      msg:"",
      // 是否显示
      isOpen:0,
      // 搜索到的数据
      AllSearchData:[]
    };
  },

  created() {
    this.getTypeData();
  },

  methods: {
    // 跳转详情页面
    toDetail(pid){
      // console.log(pid);
        this.$router.push({name: "Detail",params: {pid}});
    },
     // 输入框发生改变的时候
    isShow(val){
        if(val.length == 0){
          // this.isOpen = true;
          this.isOpen=0;
        }
        console.log('没有内容');
    },
    // 搜索商品
    search(sc){
        this.isOpen = false;
        this.axios({
        method: "GET",
        url: "/search",
        params: {
          name:sc
        }
      })
        .then((res) => {
          console.log("getSelectionData res==>",res);
          if (res.data.code === "Q001" && res.data.result.length !=0) {
            this.AllSearchData = res.data.result;
            this.isOpen = 1;
          }else if(res.data.code === "Q001" && res.data.result.length == 0){
            this.isOpen = 2;
            // Boolean(undefined) ==>false
          }
        
        })
        .catch((err) => {
            console.log(err);
        });
    },
    //获取商品类型
    getTypeData() {
      this.axios({
        method: 'GET',
        url: '/type'
      }).then(res => {
        if (res.data.code === 400) {
          //构造热卖推荐类型
          res.data.result.unshift({type: 'isHot', typeDesc: '热卖推荐'});

          this.typeData = res.data.result;

          //根据商品类型获取商品数据
          this.getProductByType(res.data.result[this.activeIndex].type);
        }
      }).catch(err => {
        
      })
    },

    //切换类型
    toggleType(index, type) {
      if (this.activeIndex === index) {
        return;
      }

      this.activeIndex = index;

      this.getProductByType(type);
    },

    //根据商品类型获取商品数据
    getProductByType(type) {
      // 

      //获取缓存数据
      let cacheData = localStorage.getItem('cacheProduct');
      // 

      cacheData = cacheData ? JSON.parse(cacheData) : {};

      //判断缓存是否过期
      //获取当前时间
      let currentTime = new Date().getTime();
      if (cacheData.expires && currentTime - cacheData.expires < 10 * 60 * 1000) {
        //当前类型的商品数据是否存在
        if (cacheData[type]) {
          //如果存在，则在缓存获取
          
          this.productData = cacheData[type];

          setTimeout(() => {
            this.isLoading = false;
          }, 100)
          return
        }
      } else {
        //如果缓存过期, 将缓存数据清空
        cacheData = {};
      }   
      let key = '';
      let value = '';
      if (type === 'isHot') {
        key = type;
        value = 1;
      } else {
        key = 'type';
        value = type;
      }

      //发起请求
      this.axios({
        method: 'GET',
        url: '/typeProducts',
        params: {
          key,
          value
        }
      }).then(res => {
        
        if (res.data.code === 500) {
          this.productData = res.data.result;

          this.isLoading = false;
          
          //首次缓存数据时，记录缓存时间
          if (!cacheData.expires) {
             //假设缓存数据有效期为1分钟
            cacheData.expires = new Date().getTime();
          }
          cacheData[type] = res.data.result;
          localStorage.setItem('cacheProduct', JSON.stringify(cacheData));

        }
      }).catch(err => {
        
      })

    },

    //查看商品详情
    viewDetail(pid) {
      this.$router.push({name: 'Detail', params: {pid}});
    }
  }
}
</script>

<style lang="less" scoped>
 .menu {
   .search-content{
     display: flex;
     flex-wrap:wrap;
   }
   /deep/ .search-item{
     padding: 10px;
     box-sizing: border-box;
     width: calc(~"50%");
   }
   .skeleton-box{
     position: fixed;
     top: 0;
     bottom: 50px;
     right: 0;
     left: 0;
     padding: 10px 0;
     background-color: #fff;
     .skeleton-menu{
       display: flex;
       height: calc(~"100% - 20px");
       margin-top: 20px;
     }
     .skeleton-menu-left{
       width: 80px;
       height: 100%;
     }
     .skeleton-menu-right{
       flex: 1;
       height: 100%;
     }
     .skeleton-item{
       margin-top: 50px;
     }
  }
   .menu-search{
      position: sticky;
      top: 0;
   }

   .menu-box{
      position: fixed;
      top: 54px;
      bottom: 50px;
      left: 0;
      right: 0;
      display: flex;
   }

   .aside-menu{
     width: 80px;
     height: 100%;
     font-size: 14px;
     overflow-y: auto;
   }

   .menu-content{
    //  width: calc(~"100% - 80px");
     flex: 1;
     height: 100%;
     background-color: #fff;
     padding: 10px;
     box-sizing: border-box;
     overflow-y: auto;
    
   }

   .aside-menu-item{
     height: 40px;
     line-height: 40px;
     text-align: center;
   }
   .aside-menu-item.active{
     background-color: #fff;
     color: #eb2403;
     font-weight: bold;
   }

   .menu-product{
      display: flex;
      flex-wrap: wrap;
   }

   .m-item{
     width: calc(~"50% - 5px");
     margin-right: 10px;
     margin-bottom: 10px;
     &:nth-child(2n){
       margin-right: 0;
     }
   }

 }
</style>
